<template>
  <div class="container-product-view-more" id="layoutMain">
    <div class="producLst-frame" id="layout1">
      <img class="producCell-frame-image" :src="productInfo.Img" alt="" />

      <div style="padding: 14px; padding-top: 0px">
        <p
          style="
            font-family: 'Inter';
            font-weight: 500;
            color: #0e59bf;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2; /* number of lines to show */
            line-clamp: 2;
            -webkit-box-orient: vertical;
          "
          class="font-size-h5"
        >
          {{ productInfo.ProductName }}
        </p>
        <p style="margin: 6px 0px" class="font-size-h6 description-product">
          {{ productInfo.ActiveElement }}
        </p>

        <button
          @click="seeMore"
          :key="componentKey"
          class="view-more font-size-h6"
          id="btViewMore"
        >
          View more
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dataProduct: {},
      componentKey: 0,
    };
  },
  props: {
    productInfo: Object,
  },
  created() {
    if (this.productInfo) {
      this.dataProduct = this.productInfo;
    }
  },
  methods: {
    seeMore() {
      if (this.dataProduct != null) {
        this.$emit("checkSeeMore", this.dataProduct);
      }
    },
  },
};
</script>

