import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

import home from "../view/home-page.vue";
import research from "../view/body/research-developments/research-development.vue";
import productLst from "../view/body/product/product-list.vue";
// import pharmacological from "../view/body/pharmacological/pharmacological.vue";
import selectProduct from "../view/body/select-products.vue"
import news from "../view/body/news/news-research.vue";
import productDetail from "../view/body/product/product-detail.vue"
import searchAll from "../view/body/search-all.vue"
import contactUs from "../view/body/contact-us/contact-us.vue"
import aboutUs from "../view/body/about-us/about-us.vue"
// import { component } from "vue/types/umd";

export const constantRoutes = [
  {
    path: '/',
    component: home,
  },
  {
    path: '/home/:glevel1',
    component: home,
  },
  {
    path: '/research',
    component: research,
  },
  {
    path: '/products',
    component: selectProduct,
  },
  {
    path: '/products/:catId/:name',
    component: productLst,
  },
  {
    path: '/news/:id',
    component: news,
  },
  {
    path:'/productdetail/:name/:id',
    component: productDetail,
  },
  {
    path:'/search/:name',
    component: searchAll,
  },
  {
    path:'/about-us',
    component: aboutUs,
  },
  {
    path:'/contact-us',
    component: contactUs,
  },
]

const createRouter = () =>
  new Router({
    // mode: 'history', // require service support
    scrollBehavior: () => ({ y: 0 }),
    routes: constantRoutes,
  });

const router = createRouter();

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter();
  router.matcher = newRouter.matcher; // reset router
}

export default router;
