    productLine,
    ProductCell,
    Productfeatured,
    ProductLineCarousel,
    VueSlickCarousel,
  },
  methods: {
    sendContact() {
      var name = document.getElementById("inputName").value.length;
      var phone = document.getElementById("inputPhone").value.length;
      var mail = document.getElementById("inputMail").value.length;
      var address = document.getElementById("inputAddress").value.length;
      var message = document.getElementById("inputMessage").value.length;
      if (
        name == 0 ||
        phone == 0 ||
        mail == 0 ||
        address == 0 ||
        message == 0
      ) {
        document.getElementById("textError").innerHTML =
          " You have not filled in the contact infomation";
        this.openDialogFail();
      } else {
        this.loadingContact = true;
        SendContactInfo({
          CustomerInfo: {
            Name: this.inputName,
            Phone: this.inputPhone,
            Email: this.inputMail,
            Message: this.inputMessage,
          }
        }).then((res) => {
          this.loadingContact = false;
          if (res.data.RespCode == 0) {
            this.openDialogSucess();
          } else {
            document.getElementById("textError").innerHTML = res.data.RespText;
            this.openDialogFail();
          }
        });
      }
    },
    openDialogFail() {
      document.getElementById("dialogLayoutRespond").style.display = "flex";
      document.getElementById("dialogSuccess").style.display = "none";
      document.getElementById("dialogFail").style.display = "block";
    },
    openDialogSucess() {
      document.getElementById("dialogLayoutRespond").style.display = "flex";
      document.getElementById("dialogSuccess").style.display = "block";
      document.getElementById("dialogFail").style.display = "none";
    },
    closeDialogRespond() {
      document.getElementById("dialogLayoutRespond").style.display = "none";
    },
    btSeeResearch() {
      this.$router.push({
        path: "/research",
      });
    },
    btSeeAllProduct() {
      this.$router.push({
        path: "/products",
      });
    },
    closeDialog() {
      document.getElementById("containerDialog").style.display = "none";
    },
    showDialog(dataProduct) {
      this.productDialog = dataProduct;
      document.getElementById("containerDialog").style.display = "flex";
      document.getElementById("containerDialog").style.opacity = 1;
    },
    getListProductHot() {
      GetProductHot({}).then((res) => {
        this.featuredProduct = res.data.Data;
      });
    },
    getNewProductLst() {
      GetProductNew({}).then((res) => {
        this.newProductLst = res.data.Data;
        // this.$store.dispatch("updateProductLst", this.newProductLst);
      });
    },
  },
  computed: {
    ...mapGetters(["productLst"]),
    // ...mapActions(["updateProductLst"]),
  },
  created() {
    this.getListProductHot();
    this.getNewProductLst();
  },
};
</script>

