<template>
  <div class="viewMore-container" id="view">
    <div id="content">
      <div
        id="imageViewMore"
        style="
          margin: 20px auto;
          margin-left: 20px;
          display: flex;
          text-align: center;
          justify-content: center;
        "
      >
        <img
          class="image-view-more"
          id="imgViewmore"
          style="width: 100%; height: fit-content"
          :src="dataProduct.Img"
          alt=""
        />
      </div>

      <div
        id="descriptionProduct"
        style="margin: 30px; margin-top: 12px; width: 65%"
      >
        <p
          class="subtitle-more font-size-h3"
          style="font-weight: 500; color: #0e59bf; margin-top: 5px"
          v-html="dataProduct.ProductName"
        ></p>
        <p class="title-more font-size-p">1.Composition</p>
        <p
          class="subtitle-more font-size-h6"
          v-html="dataProduct.Composition"
        ></p>

        <p class="title-more font-size-p">2.Dosage Form</p>
        <p
          class="subtitle-more font-size-h6"
          v-html="dataProduct.DosageForm"
        ></p>

        <p class="title-more font-size-p">3.Indication</p>
        <p
          class="subtitle-more font-size-h6"
          v-html="dataProduct.Indication"
        ></p>

        <p
          v-if="dataProduct.DosageAdministration"
          class="title-more font-size-h6"
        >
          4.Dosage Administration
        </p>
        <p
          v-if="dataProduct.DosageAdministration"
          class="subtitle-more font-size-h6"
          v-html="dataProduct.DosageAdministration"
        ></p>
      </div>
    </div>

    <div class="more-products" style="margin-top: 50px" v-if="productLstMore.length > 0">
      <div
        style="
          height: 10px;
          border-bottom: 2px solid #ff9900;
          text-align: center;
          margin: 5px auto;
        "
        class="line"
      >
        <span
          style="
            font-family: 'Inter';
            font-weight: 500;
            background-color: #dcf2ff;
            padding: 0px 20px;
            text-align: center;
          "
          class="font-size-h4"
        >
          Maybe you are interested
          <!--Padding is optional-->
        </span>
      </div>

      <div class="container-featuredProduct">
        <p
          class="font-size-h7 button-see-all"
          style="
            margin: 15px 15px 5px 0px;
            width: calc(100% - 20px);
            text-align: end;
            color: rgb(255, 153, 0);
          "
          @click="btSeeAllProduct"
        >
          Xem tất cả
          <i class="el-icon-arrow-right" style="color: rgb(255, 153, 0)" />
        </p>
        <VueSlickCarousel
          ref="carousel"
          v-bind="settings"
          class="sum-carousel"
          v-if="productLstMore.length > 0"
        >
          <feature-product
            :key="index"
            v-for="(productInfo, index) in productLstMore.slice(
              0,
              productLstMore.length - 1
            )"
            class="item-featured"
            :productInfo="productInfo"
            @checkSeeMore="getProduct($event)"
          >
          </feature-product>
        </VueSlickCarousel>
      </div>
    </div>

    <div style="height: 30px" ></div>
  </div>
</template>

<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
import { GetProductByID, GetProductByCategory } from "@/api/exportApi.js";
import FeatureProduct from "@/view/component/feature-product.vue";

export default {
  components: {  VueSlickCarousel, FeatureProduct },
  methods: {
    btSeeAllProduct() {
      this.$router.push({
        path: "/products",
      });
    },
    getProductByCat() {
      GetProductByCategory({
        CategoryID: this.dataProduct.CategoryID,
        PageNumber: 1,
        RowspPage: 10,
      }).then((res) => {
        this.productLstMore = res.data.Data;
        console.log(res.data.Data);
      });
    },
    getProductDetail() {
      GetProductByID({
        ProductID: this.$route.params.id,
      }).then((res) => {
        this.$forceUpdate();
        this.dataProduct = res.data.ProductInfo;
        this.getProductByCat();
      });
    },
    getProduct(product) {
      this.$router.push({
        path:
          "/productdetail/" +
          product.ProductName.toLowerCase().split(" ").join("-") +
          "/" +
          product.ProductID,
      });
      GetProductByID({
        ProductID: product.ProductID,
      }).then((res) => {
        this.$forceUpdate();
        this.dataProduct = res.data.ProductInfo;
        this.getProductByCat();
      });
    },
  },
  data() {
    return {
      dataProduct: {},
      productLstMore: [],
      settings: {
        arrows: true,
        dots: false,
        rows: 1,
        infinite: true,
        initialSlide: 3,
        slidesToShow: 5,
        speed: 1000,
        slidesToScroll: 1,
        swipeToSlide: true,
        autoplay: true,
        responsive: [
          {
            breakpoint: 1400,
            settings: {
              slidesToShow: 5,
              slidesToScroll: 1,
            },
          },
          {
            breakpoint: 1200,
            settings: {
              slidesToShow: 4,
              slidesToScroll: 1,
            },
          },
          {
            breakpoint: 992,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 1,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 1,
            },
          },
        ],
      },
    };
  },
  created() {
    this.getProductDetail();
  },
};
</script>

