<template>
  <div class="producCell-frame" @click="btProductLst(productCat.CategoryID)">
    <img
      class="producCell-frame-image"
      v-if="productCat.Img"
      :src="productCat.Img"
      alt=""
    />
    <img
      v-else
      class="producCell-frame-image"
      src="@/assets/img-empty.png"
      alt=""
    />
    <div style="margin: 0px; margin-top: -5px">
      <p
        class="font-size-p"
        style="padding: 8px; color: #aa0a5f; font-weight: 600"
      >
        {{ productCat.ECategoryProduct }}
      </p>
    </div>
  </div>
</template>

<script>
import { GetProductByCategory } from "@/api/exportApi.js";

export default {
  data() {
    return {
      dataProductCat: {},
      productLst:[]
    };
  },
  props: {
    productCat: Object,
  },
  methods: {
    getProductByCat(catId) {
      console.log("getData");
      GetProductByCategory({
        CategoryID: catId,
        PageNumber: 1,
        RowspPage: 1000,
      }).then((res) => {
        this.productLst = res.data.Data;
        console.log(res.data.Data);
      });
    },
    btProductLst(catId) {
      this.$router.push({
        path:
          "/products/" +
          this.productCat.CategoryID +
          "/" +
          this.productCat.ECategoryProduct.toLowerCase().split("/").join("-"),
      });
      console.log(this.productCat.CategoryID);
      this.getProductByCat(catId)
    },
  },
};
</script>

