<script>
import ProductImage from "../component/product-image.vue";
import ProductResearch from "../component/product-research.vue";
import { SearchAll } from "@/api/exportApi.js";
import FeatureProduct from "../component/feature-product.vue";

export default {
  data() {
    return {
      inputSearch: "",
      productLstSearch: [],
      productCatLstSearch: [],
      newLstSearch: [],
      productDialog: {},
    };
  },
  props: {
    productCat: Object,
    productInfo: Object,
  },
  components: {
    ProductResearch,
    ProductImage,
    FeatureProduct,
  },
  methods: {
    closeDialog() {
      document.getElementById("containerDialog").style.display = "none";
    },
    showDialog(dataProduct) {
      this.productDialog = dataProduct;
      document.getElementById("containerDialog").style.display = "flex";
      document.getElementById("containerDialog").style.opacity = 1;
    },
    getListData() {
      SearchAll({ Search: this.inputSearch }).then((res) => {
        this.productLstSearch = res.data.ProductLst;
        this.productCatLstSearch = res.data.CategoryProductLst;
        this.newLstSearch = res.data.NewLst;
        if (this.productLstSearch.length === 0 && this.productCatLstSearch.length === 0 && this.newLstSearch.length === 0){
            document.getElementById("emptyLayout").style.display = "flex"
        }
      });
    },
  },
  created() {
    this.inputSearch = this.$route.params.name;
    this.getListData();
  },
};
</script>

