<script>
import { GetCategoryProduct, SearchAll } from "@/api/exportApi.js";

export default {
  data() {
    return {
      inputSearch: "",
      productCat: {},
      productCatLst: [],
      productLstSearch: [],
      productCatLstSearch: [],
      newLstSearch: [],
      loading: true,
    };
  },

  created() {
    this.getProductCatLst();
    //console.log(this.productCatLst + " productCat")
  },
  // computed: {
  //   ...mapGetters(["productCatLst"]),
  // },
  watch: {
    inputSearch() {
      if (this.inputSearch == "") {
        this.productLstSearch = [];
        this.productCatLstSearch = [];
        this.newLstSearch = [];
      } else {
        this.searchAll();
      }
    },
  },
  props: {
    productSearch: Object,
  },
  methods: {
    openContactUs() {
      this.closeNavMenu();
      this.$router.push({
        path: "/contact-us",
      });
    },
    openAboutUs() {
      this.closeNavMenu();
      this.$router.push({
        path: "/about-us",
      });
    },
    openSearchView() {
      this.clearSuggest();
      this.$router.push({
        path: "/search/" + this.inputSearch,
      });
    },
    seeDetailProduct(item) {
      this.productLstSearch = [];
      this.$router.push({
        path:
          "/productdetail/" +
          item.ProductName.toLowerCase().split(" ").join("-") +
          "/" +
          item.ProductID,
      });
      this.clearSuggest();
    },
    searchAll() {
      SearchAll({ Search: this.inputSearch }).then((res) => {
        this.loading = false;
        this.productLstSearch = res.data.ProductLst;
        this.productCatLstSearch = res.data.CategoryProductLst;
        this.newLstSearch = res.data.NewLst;
        if (this.inputSearch == "") {
          this.productLstSearch = [];
          this.productCatLstSearch = [];
          this.newLstSearch = [];
        }
      });
    },
    btProductLst(cat) {
      console.log(cat);
      this.$router.push({
        path:
          "/products/" +
          cat.CategoryID +
          "/" +
          cat.ECategoryProduct.toLowerCase().split(" ").join("-"),
      });
    },
    openCategoryProduct() {
      this.closeNavMenu();
      this.$router.push({
        path: "/products/",
      });
    },
    getProductCatLst() {
      GetCategoryProduct({}).then((res) => {
        this.productCatLst = res.data.Data;
      });
    },
    checkSuggest() {
      const inputSearch = document.getElementById("input-search");
      var hastag = document.getElementById("hashtag-layout");
      document.getElementById("clearSearchLayout").style.display = "block";

      inputSearch.addEventListener("click", () => {
        hastag.style.display = "block";
      });
    },
    checkShow() {
      var x = document.getElementById("menu-mobile");
      var y = document.getElementById("clear-fix");
      if (x.style.display === "none" || x.style.display === "") {
        x.style.display = "block";
        y.style.display = "block";
        document.getElementById("fullMenu").style.display = "flex";
      } else {
        x.style.display = "none";
        y.style.display = "none";
        document.getElementById("fullMenu").style.display = "none";
      }
    },
    closeNavMenu() {
      var x = document.getElementById("menu-mobile");
      var y = document.getElementById("clear-fix");
      x.style.display = "none";
      y.style.display = "none";
      document.getElementById("fullMenu").style.display = "none";
    },
    btSeeProduct() {
      this.closeNavMenu();
      this.$router.push({
        path: "/products",
      });
    },
    backHome() {
      this.closeNavMenu();
      this.$router.push({ path: "/" });
    },
    btSeeProductDetail() {
      this.closeNavMenu();
      this.$router.push({
        path: "/productDetail",
      });
    },
    btSeeResearch() {
      this.closeNavMenu();
      this.$router.push({
        path: "/research",
      });
    },
    btSeeProductLst() {
      this.closeNavMenu();
      this.$router.push({
        path: "/productLst",
      });
    },

    btSeeMenuProduct() {
      var menuProduct = document.getElementById("menuProductMobile");
      var txtProduct = document.getElementById("title-product");
      var iconProduct = document.getElementById("ic-down-product");

      if (menuProduct.offsetHeight) {
        menuProduct.style.height = 0;
        txtProduct.style.color = "#fff";
        iconProduct.style.transform = "rotate(0)";
      } else {
        txtProduct.style.color = "#f6214b";
        var wrapper = document.querySelector(".menu-product-mobile");
        menuProduct.style.height = wrapper.offsetHeight + 10 + "px";
        iconProduct.style.transform = "rotate(3.142rad)";
      }
    },
    btSeeMenuPharma() {
      var menuPharma = document.getElementById("menuPharmaMobile");
      var txtPharma = document.getElementById("title-pharma");
      var iconPharma = document.getElementById("ic-down-pharma");

      if (menuPharma.offsetHeight) {
        menuPharma.style.height = 0;
        txtPharma.style.color = "#fff";
        iconPharma.style.transform = "rotate(0)";
      } else {
        txtPharma.style.color = "#f6214b";
        var wrapper = document.querySelector(".menu-pharma-mobile");
        menuPharma.style.height = wrapper.offsetHeight + 10 + "px";
        iconPharma.style.transform = "rotate(3.142rad)";
      }
    },
    toggleNavPanel(x) {
      var panel = document.getElementById(x),
        maxH = "300px";
      if (panel.style.height == maxH) {
        panel.style.height = "0px";
      } else {
        panel.style.height = maxH;
      }
    },
    clearSuggest() {
      this.productLstSearch = [];
      var hastag = document.getElementById("hashtag-layout");
      hastag.style.display = "none";
      document.getElementById("clearSearchLayout").style.display = "none";
    },
  },
};
</script>

