<template>
  <div style="background-color: #fff">
    <div class="container-product-detail" v-loading="loading">
      <img class="image-product" :src="researchDetail.Img" alt="" />
      <div class="product-detail-content">
        <div class="content">
          <p class="font-size-h3" v-html="researchDetail.EName"></p>
          <div
            class="font-size-p"
            style="margin: 0px;margin-top: 10px; width: 100%"
            v-html="researchDetail.EDescription"
          ></div>
          <div class="font-size-p eContent" style="width: 100%; margin: 0px;" v-html="researchDetail.EContent"></div>
        </div>

        <div class="productLst">
          <p
            style="color: #413bc1; font-weight: 500; margin-left: 6px"
            class="font-size-p"
          >
            PRODUCT
          </p>
          <product-image
            style="margin-top: 15px"
            :key="index"
            v-for="(productCat, index) in productCatLst"
            :productCat="productCat"
          ></product-image>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import productImage from "@/view/component/product-image.vue";
import { GetCategoryProduct, GetResearchNewsByID } from "@/api/exportApi.js";
export default {
  data() {
    return {
      productCatLst: [],
      researchDetail: {},
      loading: false,
    };
  },
  props: {
    productCat: Object,
  },
  created() {
    this.getProductCatLst();
    this.getResearchDetail();
  },
  components: { productImage },
  methods: {
    getProductCatLst() {
      GetCategoryProduct({}).then((res) => {
        this.productCatLst = res.data.Data;
      });
    },
    getResearchDetail() {
      this.loading = true;
      GetResearchNewsByID({ ID: this.$route.params.id }).then((res) => {
        this.researchDetail = res.data.NewInfo;
        this.loading = false;
      });
    },
  },
};
</script>

