<script>
import { SendContactInfo } from "@/api/exportApi.js";
export default {
  data() {
    return {
      inputName: "",
      inputMail: "",
      inputPhone: "",
      inputAddress: "",
      inputMessage: "",
      loading: false,
    };
  },
  methods: {
    sendContact() {
      console.log("send");
      var name = document.getElementById("inputName").value.length;
      var phone = document.getElementById("inputPhone").value.length;
      var mail = document.getElementById("inputMail").value.length;
      var address = document.getElementById("inputAddress").value.length;
      var message = document.getElementById("inputMessage").value.length;
      if (
        name == 0 ||
        phone == 0 ||
        mail == 0 ||
        address == 0 ||
        message == 0
      ) {
        document.getElementById("textError").innerHTML =
          " You have not filled in the contact infomation";
        this.openDialogFail();
      } else {
        console.log("not null");
        this.loading = true;
        SendContactInfo({CustomerInfo : {
            Name: this.inputName,
            Phone: this.inputPhone,
            Email: this.inputMail,
            Message: this.inputMessage,
          }}).then((res) => {
          this.loading = false;
          if (res.data.RespCode == 0) {
            this.openDialogSucess();
          } else {
            document.getElementById("textError").innerHTML = res.data.RespText;
            this.openDialogFail();
          }
        });
      }
    },
    openDialogFail() {
      document.getElementById("dialogLayout").style.display = "flex";
      document.getElementById("dialogSuccess").style.display = "none";
      document.getElementById("dialogFail").style.display = "block";
    },
    openDialogSucess() {
      document.getElementById("dialogLayout").style.display = "flex";
      document.getElementById("dialogSuccess").style.display = "block";
      document.getElementById("dialogFail").style.display = "none";
    },
    closeDialog() {
      document.getElementById("dialogLayout").style.display = "none";
    },
  },
};
</script>
