const storeConfig = {
    state:{
        productInfo: {},
        productLst: [],
        productCatLst: [],
        productCat: {},
    },
    getters: {
        productInfo : state => state.productInfo,
        productLst: state => state.productLst,
        productCat: state => state.productCat,
        productCatLst: state => state.productCatLst
    },
    mutation :{
        setProduct(state, productInfo){
            state.productInfo = productInfo
        },
        setProductLst(state, productLst){
            state.productLst = productLst
        },
        setProductCat(state, productCat){
            state.productCat = productCat
        },
        setProductCatLst(state, productCatLst){
            state.productCatLst = productCatLst
        }
    },
    action: {
        updateProduct({commit}, productInfo){
            commit('setProduct', productInfo)
        },
        updateProductLst({commit}, productLst){
            commit('setProductLst', productLst)
        },
        updateProductCat({commit}, productCat){
            commit('setProductCat', productCat)
        },
        updateProductCatLst({commit}, productCatLst){
            commit('setProductCatLst', productCatLst)
        }
    }

}

export {storeConfig}