<script>
import productViewMore from "../../component/product-view-more.vue";
import { GetProductByCategory } from "@/api/exportApi.js";

export default {
  data() {
    return {
      id: "",
      productLst: [],
      categoryName: "",
      categoryImg: "",
      productID: "",
      productDialog: {},
      pageSize: 12,
      currentPage: 1,
      totalRows: 0,
      loading: false,
    };
  },
  watch: {
    currentPage() {
      this.getProductByCat(this.$route.params.catId);
    },
  },
  props: {
    productInfo: Object,
    productCat: Object,
  },
  components: { productViewMore },
  methods: {
    closeDialog() {
      document.getElementById("containerDialog").style.display = "none";
    },
    showDialog(dataProduct) {
      console.log("showDialog" + dataProduct.ProductName);
      this.productDialog = dataProduct;
      document.getElementById("containerDialog").style.display = "flex";
      document.getElementById("containerDialog").style.opacity = 1;
    },
    getProductByCat() {
      this.loading = true
      console.log("getData");
      GetProductByCategory({
        CategoryID: this.id,
        PageNumber: this.currentPage,
        RowspPage: this.pageSize,
      }).then((res) => {
        this.productLst = res.data.Data;
        this.categoryName = res.data.CategoryName;
        this.categoryImg = res.data.CategoryLogo;
        this.totalRows = res.data.TotalRows;
        // console.log(this.productLst);
               this.loading = false
      });
    },
    btSeeMoreLst() {
      this.pageNumber += 1;
      this.getProductByCat(this.id);
    },
  
  },
  // computed: {
  //   ...mapGetters(["productLst", "productInfo"]),
  // },
  created() {
    this.id = this.$route.params.catId;
    this.getProductByCat(this.$route.params.catId);
    console.log(this.$route.params.catId);
  },
};
</script>

